  manageGallery() : void
  {
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }



     let   options       : any	     = {
       idempresa:this.idempresa,
       tipo:'Equipo de computo',
     nombre : this.form.controls['nombre'].value,

     cui: this.form.controls['cui'].value,

     monto: this.form.controls['monto'].value,
     plazo: this.form.controls['plazo'].value,
     interes: this.form.controls['interes'].value,
     estado : 'Solicitando prstamo',
      codpersonal:this.carne,
      requisito1  		: this.f1l,
      requisito2  		: this.f2l,
      requisito3  		: this.f3l,
      requisito4  		: this.f4l,
      requisito5  		: this.f5l,
      requisito6  		: this.f6l,
      requisito7  		: this.f7l,
      requisito8  		: this.f8l,
      requisito9  		: this.f9l,
      requisito10  		: this.f10l,

     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' solprestamos '}
    };


     if(this.navParams.get("record")==null)
     {
          if(options){
            this.authService.createReg('',options,'solprestamos').then((result) => {
              if (!this.form.valid) { return; }
              this.viewCtrl.dismiss(result);
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
          }
     }
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'solprestamos').then((result) => {
           if (!this.form.valid) { return; }
           console.log(result)
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }

