  updateProfileImage(op) {
    this.opimg=op;

    switch (op)
    {
        case 1: this.fileInput.nativeElement.click(); break;
        case 2: this.fileInput2.nativeElement.click(); break;
        case 3: this.fileInput3.nativeElement.click(); break;
        case 4: this.fileInput4.nativeElement.click(); break;
        case 5: this.fileInput5.nativeElement.click(); break;
        case 6: this.fileInput6.nativeElement.click(); break;
        case 7: this.fileInput7.nativeElement.click(); break;
        case 8: this.fileInput8.nativeElement.click(); break;
        case 9: this.fileInput9.nativeElement.click(); break;

        default:

            break;
    }





  }


  takePhotograph(op) : void
  {
   if (Camera['installed']()) {
     this._IMAGE
     .takePhotograph()
     .then((image)=>
     {
      if(op==1){    this.f1a = 'data';  this.f1 	= image.toString()};
      if(op==2)  {  this.f2a = 'data';  this.f2 	= image.toString()};
      if(op==3) {   this.f3a = 'data';  this.f3 	= image.toString();}
      if(op==4) {   this.f4a = 'data';  this.f4 	= image.toString();}
      if(op==5) {   this.f5a = 'data';  this.f5 	= image.toString();}
      if(op==6) {  this.f6a = 'data';   this.f6 	= image.toString();}
      if(op==7) {   this.f7a = 'data';  this.f7 	= image.toString();}
      if(op==8) {  this.f8a = 'data';   this.f8 	= image.toString();}
      if(op==9) {   this.f9a = 'data';  this.f9 	= image.toString();}
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
    switch (op)
    {
        case 1: this.fileInput.nativeElement.click(); break;
        case 2: this.fileInput2.nativeElement.click(); break;
        case 3: this.fileInput3.nativeElement.click(); break;
        case 4: this.fileInput4.nativeElement.click(); break;
        case 5: this.fileInput5.nativeElement.click(); break;
        case 6: this.fileInput6.nativeElement.click(); break;
        case 7: this.fileInput7.nativeElement.click(); break;
        case 8: this.fileInput8.nativeElement.click(); break;
        case 9: this.fileInput9.nativeElement.click(); break;

        default:

            break;
    }

   }
  }


